import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import './index.css';
import mbp from "./manage.blog.posts";
import * as serviceWorker from './serviceWorker';



const Home = React.lazy(() => import("./components/Home" /* webpackChunkName: "home" */));

const About = React.lazy(() => import("./components/About" /* webpackChunkName: "about" */));


class App extends React.Component {
  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <Switch>

          <Route exact path="/">
            <React.Suspense fallback={<p>Loading...</p>}>
              <Home/>
            </React.Suspense>
          </Route>

          <Route exact path="/about">
            <React.Suspense fallback={<p>Loading...</p>}>
              <About/>
            </React.Suspense>
          </Route>

          {mbp.map((b, i) => {
            const Component = React.lazy(() => import(`./components/blog/${b.file}`));
            return (
              <Route key={i} exact path={`/blog/${b.url}`}>
                <React.Suspense fallback={<p>Loading...</p>}>
                  <Component/>
                </React.Suspense>
              </Route>
            );
          })}

        </Switch>
      </Router>
    );
  }
}


ReactDOM.render(<App />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
// serviceWorker.register();
